.login {
    padding: 10px;
    /*background-color: #bbbbbb;*/
    margin: 250px auto 0 auto;
    width: 500px;
    height: 300px;
}

.login-id-box {
    margin: 100px auto 0 auto;
    text-align: center;
    height: 30px;
}

.login-id-box text {
    margin: 5px auto;
}

.id {
    height: 20px;
}

.login-pwd-box {
    margin: 0 auto;
    text-align: center;
    height: 30px;
}

.login-pwd-box text {
    margin: 5px auto;
}

.pwd {
    height: 20px;
}

.login-btn-box {
    margin: 15px auto;
    text-align: center;
}

.login-btn {
    width: 80px;
    height: 30px;
    margin: 0 0 0 20px;
}

.login-cancel {
    margin: 0 0 0 20px;
    width: 80px;
    height: 30px;
}