.main-header {
    width: 100%;
    height: 100px;
    border: 1px solid black;
}

.main-login-box {
    width: 100px;
    height: 30px;
    float: right;
    margin-right: 30px;
    margin-left: 40px;
    margin-top: 35px;
}

.main-login-box button {
    width: 100%;
    height: 100%;
}

.main-join-box {
    width: 100px;
    height: 30px;
    float: right;
    margin-top: 35px;

}

.main-join-box button {
    width: 100%;
    height: 100%;
}

.main-body {
    height: 800px;
}

.main-book-search {
    width: 400px;
    text-align: center;
    vertical-align: middle;
    margin: 200px auto;
    padding: 100px 0;
    font-size: 25px;
    font-weight: bold;
}

.book-search-box {
    height: 25px;
    margin-right: 10px;
    font-size: 15px;
}

.book-search-btn {
    height: 30px;
}

.icon {
    border: 1px solid black;
    width: 50px;
    height: 50px;
    text-align: center;
    vertical-align: center;
    margin: 25px 330px 25px auto;
    float: right;
    display: inline;
}

.myPage {
    width: 120px;
    height: 50px;
    display: inline;
    margin: 25px -200px 25px auto;
    float: right;
}

.myPage-btn {
    width: 100%;
    height: 100%;
    font-size: 18px;
    font-weight: bold;
    border-radius: 10px;
    background-color: #61dafb;
}

.logout {
    width: 120px;
    height: 50px;
    display: inline;
    float: right;
    margin: 25px -350px 25px auto;
}

.logout-btn {
    width: 100%;
    height: 100%;
    font-size: 18px;
    font-weight: bold;
    border-radius: 10px;
}